.squiggly-arrow {
    height: 100%;
    width: 100%;

    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto 1fr auto 1fr auto;
}

.squiggly-arrow div {
    background-image: url(/src/squiggly-arrow.svg);
    background-repeat: no-repeat;
}

.squiggly-arrow div:nth-child(1) {
    grid-column: 1 / 1;
    grid-row: 1 / 1;

    width: 6px;
    height: 2px;
    background-position: 1px 0px;
}

.squiggly-arrow div:nth-child(2) {
    grid-column: 1 / 1;
    grid-row: 2 / 2;

    width: 6px;
    /* Think of this as height: 100% but for elements in a CSS grid */
    align-self: stretch;
    background-position: 1px -2px;
}

.squiggly-arrow div:nth-child(3) {
    grid-column: 1 / 1;
    grid-row: 3 / 3;

    width: 21px;
    height: 28px;
    background-position: 1px -435px;
}

.squiggly-arrow div:nth-child(4) {
    grid-column: 2 / 2;
    grid-row: 3 / 3;

    /* Think of this as width: 100% but for elements in a CSS grid */
    justify-self: stretch;
    height: 28px;
    background-position: -30px -435px;
}

.squiggly-arrow div:nth-child(5) {
    grid-column: 3 / 3;
    grid-row: 3 / 3;

    width: 35px;
    height: 28px;
    object-fit: none;
    background-position: -1904px -435px;
}

.squiggly-arrow div:nth-child(6) {
    grid-column: 3 / 3;
    grid-row: 4 / 4;

    width: 35px;
    /* Think of this as height: 100% but for elements in a CSS grid */
    align-self: stretch;
    background-position: -1904px -478px;
}

.squiggly-arrow div:nth-child(7) {
    grid-column: 3 / 3;
    grid-row: 5 / 5;

    width: 35px;
    height: 21px;
    background-position: -1904px -1065px;
}
