.squiggly-arrow {
  grid-template-rows: auto 1fr auto 1fr auto;
  grid-template-columns: auto 1fr auto;
  width: 100%;
  height: 100%;
  display: grid;
}

.squiggly-arrow div {
  background-image: url("squiggly-arrow.f5f66043.svg");
  background-repeat: no-repeat;
}

.squiggly-arrow div:first-child {
  background-position: 1px 0;
  grid-area: 1 / 1 / 1 / 1;
  width: 6px;
  height: 2px;
}

.squiggly-arrow div:nth-child(2) {
  background-position: 1px -2px;
  grid-area: 2 / 1 / 2 / 1;
  align-self: stretch;
  width: 6px;
}

